.o-logo{
    background-color: #212121;
    padding: 5% 0 0 0;
}

.o-containerImgLogo{
    display: flex;
    align-items: center;
    justify-content: center;
    
}

.o-containerImg{
    display: flex;
    align-items: center;
    justify-content: center;
}

.o-imgLogo{
    width: 60%;
}

.o-imgImage{
    width: 100%;
}
.o-containerText{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}



.o-text h1{
    text-align: center;
    color:#fff;
    font-size: 50px;
    font-weight: normal;
}

.o-text p{
    text-align: center;
    color:rgba(255, 255, 255, 0.6);
    padding: 5% 20%;
    font-size: 16px;
}

@media (max-width: 600px) {
    .o-containerImg{
        display: none;
    }
    .o-logo{
        padding: 10% 5%;
    }

    .o-text h1{
        font-size: 24px !important;
    }

    .o-text p {
        font-size: 14px;
        padding: 2% 10%;
    }
    
}

@media (max-width: 1000px) {
    
    .o-text h1{
        font-size: 35px;
    }

    .o-text p {
        padding: 2% 10%;
    }
    
}

